/* The container */
.container {
  color: #ffffff;
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
  background-color: hsl(204, 80%, 71%);
}

/* When the radio button is checked, add a blue background */
.container input:checked ~ .checkmark {
  background-color: hsl(204, 100%, 50%);
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.container .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
  position: absolute;
}

.h1 {
  font-size: 60px;
  text-decoration: underline;
  text-decoration-color: hsl(204, 100%, 50%);
  -webkit-text-underline-position: under;
  text-underline-position: under;
  text-align: center;
  color: #ffffff;
  margin: 10px 0px 10px 0px;
}

.h3 {
  font-size: 35px;
  text-decoration: underline;
  text-decoration-color: hsl(204, 100%, 50%);
  -webkit-text-underline-position: under;
  text-underline-position: under;
  margin-left: 5px;
  padding-top: 2px;
  color: rgb(255, 255, 255);
}

.p {
  font-size: 33px;
  text-align: center;
  padding: 5px;
  color: #ffffff;
}

.underlying {
  display: flex;
}

.underlying > div {
  width: 230px;
  text-align: left;
  line-height: 15px;
}

.form-container {
  display: flex;
  justify-content: space-between;
  width: 850px;
  line-height: 18px;
  margin-bottom: 40px;
}

.strike-expiry {
  display: flex;
  justify-content: space-between;
  width: 770px;
}

.side {
  margin-left: 50px;
  margin-right: 40px;
}

.type {
  margin-right: 50px;
}

.button {
  width: 200px;
  background-color: #ffffff;
  border-radius: 13px;
  font-weight: bold;
  color: hsl(204, 100%, 50%);
  text-align: center;
  display: inline-block;
  font-size: 25px;
  transition-duration: 0.2s;
  box-shadow: 0 1px 1px hsl(204, 100%, 50%);
}

.button:hover {
  background-color: hsl(204, 100%, 50%);
  color: white;
}

.button:active {
  box-shadow: 0 2px rgb(107, 106, 106);
  transform: translateY(2px);
}

.calculate_button:hover {
  background-color: hsl(204, 100%, 50%);
  color: white;
}

.calculate_button:active {
  box-shadow: 0 2px rgb(107, 106, 106);
  transform: translateY(2px);
}

.calculate_button {
  width: 200px;
  height: 70px;
  margin-top: 50px;
  background-color: #ffffff;
  border-radius: 13px;
  font-weight: bold;
  color: hsl(204, 100%, 50%);
  text-align: center;
  display: inline-block;
  font-size: 25px;
  transition-duration: 0.2s;
  box-shadow: 0 1px 1px hsl(204, 100%, 50%);
}

.form {
  width: 200px;
  height: 40px;
  margin-top: 10px;
  background-color: #1a1a1a57;
  border-radius: 13px;
  color: #ffffff;
  font-size: x-large;
  outline: 0;
}

.date_form {
  text-align: center;
  font-size: x-large;
  width: 230px;
  height: 45px;
  margin-top: 10px;
  color: #ffffff;
  background-color: rgba(0, 153, 255, 0.733);
  border-radius: 13px;
}

